<template>

   <v-layout row wrap>

    
  <div id="informe" class="box-style">
  
  <v-card class="white card-act pb-4">
        <v-card-title
          class="headline  text text-item-input white--text header_act">
          Informe Diario Parqueadero
        </v-card-title>

        <v-card-text class="text-md-center text-sm-center text-xs-center header_act">

            <v-layout d-block>
           



             <v-form ref="form">
              <v-flex xs12 md12 sm12 class="text-md-center mt-4">
              <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="date"
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >

       
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="fecha_inicial"
            label="Fecha Inicial"
            prepend-icon="event"
            readonly
            v-on="on"
          ></v-text-field>

        </template>
        <v-date-picker v-model="fecha_inicial" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu=false">Cancel</v-btn>
          <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
        </v-date-picker>
      </v-menu>
                             </v-flex>
              <v-flex xs12 md12 sm12 class="text-md-center mt-4">
              <v-menu
        ref="menu2"
        v-model="menu2"
        :close-on-content-click="false"
        :return-value.sync="date"
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="fecha_final"
            label="Fecha Final"
            prepend-icon="event"
            readonly
            v-on="on"
          ></v-text-field>
          <v-flex xs12 sm6 md12 class="mt-2">
                <v-select
                 class="selectField text text-sub--itemsub "
                  v-model="t_formato"
                  :items="['Excel']"
                  label="Formato archivo"
                ></v-select>
              </v-flex>

                <v-flex xs12 sm6 md12 class="mt-2">
                   <v-select
                  class="selectField text text-sub--itemsub "
                  v-model="informe"
                  :items="informes"
                  item-text="desc"
                  item-value="id"
                   label="Tipo informe"
                  
                ></v-select>
              </v-flex>


        </template>
        <v-date-picker v-model="fecha_final" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu=false">Cancel</v-btn>
          <v-btn text color="primary" @click="$refs.menu2.save(date)">OK</v-btn>
        </v-date-picker>
      </v-menu>



             
              </v-flex>
           </v-form>
         </v-layout>
      </v-card-text>
     <v-divider></v-divider>
     <v-card-actions>
     <v-spacer></v-spacer>
     <v-flex d-flex text-md-right>
         <v-btn  color="blue darken-1"  @click="validador()">Generar Informe</v-btn>
     </v-flex>
     </v-card-actions>
     </v-card> 
  </div>


<v-snackbar
         id="modalig"
         class="mss-modal  v-snack--active2"
         v-model='show'
         top

       >
       <v-flex  md12 d-flex>
           <span class="text text-sub--item  text-md-center text-xs-center">{{texto}}</span>
       </v-flex>
       
     
    </v-snackbar>

 <pre>
    {{$data}}
   </pre>

  </v-layout>
</template>
<script>
import jsPDF from 'jspdf';
import 'jspdf-autotable';
import XLSX from 'xlsx';

export default {

    data:()=>({
        fecha_inicial : null,
        fecha_final : null,
        t_formato : "Excel",
        informe : null,
        texto : null,
        timeout:3000,
        show:false,
        registros:[],
        informes : [
         {
           id : 1,
           desc : "Arqueo diario",
         },
         {
           id : 2,
           desc : "Vehiculos en parqueadero",
         },
         {
           id : 3,
           desc : "Listado Preferenciales",
         },
       ],
    }),

   methods:{
async carga()
    {

          var vm = this;
          await this.axios({
           method: 'post',
           url: 'http://107.21.217.207:8000/admin/informe_diario',
            data: {
              fecha_inicial : vm.fecha_inicial,
              fecha_final :  vm.fecha_final
            }
          }).then(function (response) {
              vm.registros = response.data.info;
              console.log("consulta: " + vm.registros);
          })
          .finally(function () {
           if (vm.t_formato == 'Excel')
           {
               vm.crearExcel(); 
           }
          });
     
    },

async infoparqueo()
    {

          var vm = this;
          await this.axios({
           method: 'post',
           url: 'http://107.21.217.207:8000/admin/informe_parqueos',
            
          }).then(function (response) {
            console.log("consulta: " + vm.registros);
              vm.registros = response.data.info;
                          
          })
          .finally(function () {
            
            if (vm.registros.length == 0)
             {
               vm.texto = "No existen registros ...";
               vm.show = true;
             }
            else
            vm.crearExcel2(); 
          });
     
    },

async preferencial()
    {

          var vm = this;
          await this.axios({
           method: 'get',
           url: 'http://107.21.217.207:8000/admin/Infopreferencial',
            
          }).then(function (response) {
            console.log("consulta: " + vm.registros);

              vm.registros = response.data.info;
              console.log(vm.registros.length);
              
          })
          .finally(function () {
            vm.crearExcel3(); 
          });
     
    },

      validador()
    {
       switch(this.informe) {
           case 2:
                  this.infoparqueo();
                 break;

           case 3 :
                 this.preferencial();
                 break;

           case 1 :
                  if ((this.fecha_final !== null) && (this.fecha_inicial !== null))
                  {
                      if (this.fecha_inicial > this.fecha_final)
                        {
                          this.texto = "La fecha inicial no puede ser mayor a la fecha final";
                          this.show = true;
                          this.fecha_inicial = null;
                        }
                      else
                        this.carga();
                   }
                   else
                    {
                      this.texto = "No puede exister campos vacios";
                      this.show = true;
                    }
                  break;
       }   
       
    },

async crearExcel()
    {
      var ws_data = this.registros;
      let data = XLSX.utils.json_to_sheet(ws_data);
      data['A1'].v = 'Placa';
      data['B1'].v = 'Entrada';
      data['C1'].v = 'Salida';
      data['D1'].v = 'Duracion';
      data['E1'].v = 'Valor Cobrado';
      data['F1'].v = 'Iva';
      data['G1'].v = 'Total';
      data['H1'].v = 'Operador';
      data['I1'].v = 'Tarifa';
      
      const workbook = XLSX.utils.book_new();
      const filename = 'informe diario parqueo';
      XLSX.utils.book_append_sheet(workbook, data, filename);
      XLSX.writeFile(workbook, `${filename}.xlsx`);
    },

    async crearExcel2()
    {
      var ws_data = this.registros;
      let data = XLSX.utils.json_to_sheet(ws_data);
      data['A1'].v = 'Placa';
      data['B1'].v = 'Entrada';
      data['C1'].v = 'Hora';
      data['D1'].v = 'Minutos';
      data['E1'].v = 'Dias';
      
      const workbook = XLSX.utils.book_new();
      const filename = 'informe vehiculos parqueadero';
      XLSX.utils.book_append_sheet(workbook, data, filename);
      XLSX.writeFile(workbook, `${filename}.xlsx`);
    },

    async crearExcel3()
    {
      var ws_data = this.registros;
      let data = XLSX.utils.json_to_sheet(ws_data);
      data['A1'].v = 'Placa';
      data['B1'].v = 'Valor';
      data['C1'].v = 'Descripcion';
      data['D1'].v = 'Tipo tarifa';

      
      const workbook = XLSX.utils.book_new();
      const filename = 'informe vehiculos preferencial';
      XLSX.utils.book_append_sheet(workbook, data, filename);
      XLSX.writeFile(workbook, `${filename}.xlsx`);
    },
   }
}
    </script>
