async crearExcel()
    {
      var ws_data = this.registros;
      let data = XLSX.utils.json_to_sheet(ws_data);
      data['A1'].v = 'Placa';
      data['B1'].v = 'Entrada';
      data['C1'].v = 'Salida';
      data['D1'].v = 'Duracion';
      data['E1'].v = 'Tarifa';
      data['F1'].v = 'Operador';
      
      const workbook = XLSX.utils.book_new();
      const filename = 'informe diario parqueo';
      XLSX.utils.book_append_sheet(workbook, data, filename);
      await XLSX.writeFile(workbook, `${filename}.xlsx`);
    },

   
