<template>
  <v-layout row wrap>
    <div id="home" class="home">
      <div class="home-content">
        <v-layout row wrap>

          <v-flex d-flex md12 align-center class="">

            <div class="d-flex nav-hed">
             <v-img :src="require('../assets/logo_parqueo.svg')" class="logo mt-2 " contain height="80">
              </v-img> 
              <h2 id="" class="text text-title white--text title-logo mt-4 "><span>PARQUEADERO PRIVADO</span>
               </h2>
            </div>

          </v-flex>

          <v-flex d-flex xs12 sm12 md6 align-center class="section-info animatable-init">
            <div class="">


              <h1 align-center
                class="headling font-weight-bold white--text text-md-center text-sm-center text-xs-center text text-sub text-d">
                Bienvenido
              </h1><br>
              <blockquote class="white--text  blockquote  text-sm-center text-xs-center text-md-center text ">
                 <b>Sistema integrado Parqueadero privado... </b>
                </blockquote>
                
            </div>

          </v-flex>

          <v-flex  id="peoplebox" d-flex xs12 sm12 md6 align-center>
            <v-img :src="require('../scss/resources/terminal.png')"
              class="mt-5 img-people mt-sm-0  animatable-init " contain height="500">
            </v-img>
          </v-flex>


          <v-flex d-flex xs12 sm12 md6 align-center class="section-info ">
            <div class="text-xs-center">
              <v-btn class="button white blue--text button-h" round color="secundary" dark @click.stop="open()">Ingresar
              </v-btn>
            </div>
          </v-flex>

        </v-layout>
      </div>
    </div>

        <!--login-->
    <div id="login" class="none">


      <v-layout d-flex row justify-center>
        <v-dialog id="vdialog" v-model="login" persistent class="v-dialoger">
          <login @desabilitar="desa(boolean)"></login>
        </v-dialog>
      </v-layout>
    </div>


   

  </v-layout>
</template>

<script>
  import login from '../components/login'
  

  export default {
    name: 'App',

    data() {
      return {
        isActive: '',
        onActive: '',
        register: false,
        login: false,
        valor: 50,
        dialog: false,
        openPoliticas: false,
        openTerminos: false

        //
      }
    },
    components: {
      login,
      
    },


    methods: {
      desa(boolean) {
        this.login = boolean
      },

      open: function () {
        var logeg = document.getElementById('login')
        logeg.classList.remove("none");
        logeg.classList.add("block");
        this.login = true;
      },




      animate: function () {



        window.addEventListener('scroll', function () {
          let ele = document.querySelectorAll('.animatable');
          let pos;
          let cor;
          for (let i = 0; i < ele.length; i++) {
            pos = ele[i];
            cor = pos.getBoundingClientRect().top;
            if (cor < window.innerHeight) {
              pos.classList.add('slide-up');
            } else {
              pos.classList.remove('slide-up');
            }

          }


        })

      },

      openModal(item) {
        if (item == 'politcs') {
          this.openPoliticas = true;

        }

        if (item == 'terminos') {
          this.openTerminos = true;
        }

      },
      closetModal(item) {
        if (item == 'politcs') {
          this.openPoliticas = false;

        }

        if (item == 'terminos') {
          this.openTerminos = false;
        }

      },

      registerDirection(){
        this.$router.push('/registro')
      }


    },
    mounted() {

      this.animate();
      //this.forming();
           window.addEventListener('scroll', function () {
          let ele = document.getElementById('video');
          let pos;
          let cor;
     
            pos = ele;
            cor = pos.getBoundingClientRect().top;
            if (cor < window.innerHeight) {
              pos.play();
            } else {
              pos.pause();
            }

          

        })
    }
  }
</script>
<style>
  .in-scroll {
    opacity: 0;
  }

  .scrolling {
    opacity: 0;

    animation: scroling 3s both;
  }

  @keyframes scroling {
    to {

      opacity: 1;
    }
  }
</style>