  <v-layout  >
<div id="profile" class="box-style box-style-l ">

<div id="count" class="">
  <i class="fas fa-address-card icon-inf"></i>
  <h3 class="text text-sub--itemsub blue--text label-item">Crear Usuario</h3>
  

<div class="box-input box-input-l slide-right">
      <label  for="nombre" class="headling-input text text-item-input mb-2">Nombre</label>
    <input type="text" name="" value="" v-model="nombre" required>
    <label for="" class="text text-item-input label"></label>
    <span class="fa fa-user icon-set  icon-set-l"></span>
     
 </div>

 <div class="box-input box-input-l slide-right">
      <label  for="apellido" class="headling-input text text-item-input mb-2">Apellidos</label>
    <input type="text" name="" value="" v-model="apellido" required>
    <label for="" class="text text-item-input label"></label>
    <span class="fa fa-user icon-set  icon-set-l"></span>
     
 </div>

<div class="box-input box-input-l slide-right">
      <label  for="usuario" class="headling-input text text-item-input mb-2">Usuario</label>
    <input type="text" name="" value="" v-model="usuario" required>
    <label for="" class="text text-item-input label"></label>
    <span class="fa fa-user icon-set  icon-set-l"></span>
     
 </div>

<div class="box-input box-input-l slide-right">
      <label  for="password" class="headling-input text text-item-input mb-2">Password</label>
    <input type="password" name="" value="" v-model="password" required>
    <label for="" class="text text-item-input label"></label>
    <span class="fa fa-user icon-set  icon-set-l"></span>
     
 </div>

 <div class="box-input box-input-l slide-right">
      <label  for="c_password" class="headling-input text text-item-input mb-2">Confirmar Password</label>
    <input type="password" name="" value="" v-model="c_password" required>
    <label for="" class="text text-item-input label"></label>
    <span class="fa fa-user icon-set  icon-set-l"></span>
     
 </div>



   
  </div>


  <v-flex d-flex md4 xs10 mt-5  >

     <v-btn  class="btn-float btn-float-left-2 btn-float-l-2" color="blue" @click="crear()">Crear Usuario</v-btn>
  </v-flex>

  <v-snackbar
         id="modalig"
         class="mss-modal  v-snack--active2"
         v-model='show'
         top

       >
       <v-flex  md12 d-flex>
       <!--<v-img class="slide-right mr-2" v-bind:src="require('../scss/resources/SVG/'+pathIcon)" width="80"></v-img>-->
       <span class="text text-sub--item  text-md-center text-xs-center">{{texto}}</span>
       </v-flex>
       
     
    </v-snackbar>


</div>




