</template>

<script>

export default {

  data(){
    return{
    password:"",
    c_password:"",
    usuario: "",
    nombre : "",
    apellido: "",
    texto : null,
    show : false,
    pathIcon:'active.svg',
    show_password: false,

    }
  },
   
methods: {
    
   async crear()
    {
    if ((this.nombre !== "") && (this.apellido !== "") && (this.usuario !== "") && (this.password !== "") && (this.c_password !== ""))
      {
       this.password = this.password.trim();
       this.c_password = this.c_password.trim();
       if (this.password !== this.c_password)
           {
             this.texto = "Los password no coinciden ...";
             this.show = true;
             
           }
           else
            {
               var vm = this;
               await this.axios({
               method: 'post',
               url: 'http://107.21.217.207:8000/admin/crear_Usuario',
               data: {
                 email :  vm.usuario,
                 password : vm.password,
                 nombre : vm.nombre,
                 apellidos : vm.apellido,
                }
               })
               .then(function (response) {
                  console.log(response.data);
                  vm.texto = response.data.info;
                  vm.show = true;
                  vm.usuario = "";
                  vm.password = "";
                  vm.nombre = "";
                  vm.apellido = "";
               });
            }
      }
     else
       {
          this.texto = "Hay campos vacios ..."
          this.show = true;
        }
    }
    
    }
}

</script>
