<script>

export default {

  data(){
    return{
    cliente : localStorage.getItem("cedula"),
    password:"",
    password2:"",
    old_password:"",
    usuario: null,
    texto : null,
    show : false,
    pathIcon:'active.svg',
    show_password: false,

    }
  },
   
mounted() {
    
},
methods: {
    
   async cambios()
    {
      if ((this.password !== "") && (this.password2 !== "") && (this.old_password !== ""))
      {
       this.password = this.password.trim();
       this.password2 = this.password2.trim();
       this.old_password = this.old_password.trim();
          if (this.password !== this.password2)
           {
             this.texto = "Los password no coinciden ...";
             this.show = true;
             
           }
           else
            {
               var vm = this;
               await this.axios({
               method: 'post',
               url: 'http://107.21.217.207:8000/admin/change',
               data: {
                 email :  vm.$cookies.get("user"),
                 old_password :  vm.old_password,
                 password : vm.password,
              },
               })
               .then(function (response) {
                  console.log(response.data);
                  vm.texto = response.data.info;
                  vm.show = true;
               });
            }
      }
     else
       {
          this.texto = "Hay campos vacios ..."
          this.show = true;
        }
    }
    
    }
}

