<template>
  <v-layout  >
<div id="profile" class="box-style box-style-l ">

<div id="count" class="">
  <i class="fas fa-address-card icon-inf"></i>
  <h3 class="text text-sub--itemsub blue--text label-item">Crear Cliente Preferencial</h3>
  

<div class="box-input box-input-l slide-right">
      <label  for="nombre" class="headling-input text text-item-input mb-2">Placa</label>
    <input type="text" name="" value="" v-model="placa" required>
    <label for="" class="text text-item-input label"></label>
    <span class="fa fa-user icon-set  icon-set-l"></span>
     
 </div>

 <div class="box-input box-input-l slide-right">
      <label  for="valor" class="headling-input text text-item-input mb-2">Valor parqueo</label>
    <input type="number" name="" value="" v-model="valor" required>
    <label for="" class="text text-item-input label"></label>
    <span class="fa fa-user icon-set  icon-set-l"></span>
     
 </div>

<div class="box-input box-input-l slide-right">
      <label  for="comentario" class="headling-input text text-item-input mb-2">Comentario</label>
    <input type="text" name="" value="" v-model="concepto" required>
    <label for="" class="text text-item-input label"></label>
    <span class="fa fa-user icon-set  icon-set-l"></span>
     
 </div>


 
          
            <v-flex xs12 sm6 md12 class="mt-2">
                <v-select

                class="selectField text text-sub--itemsub "
                  v-model="tari_seleccionada"
                  :items="tarifas"
                  item-text="concepto"
                  item-value="id"
                   label="Tipo de Tarifa*"
                  
                ></v-select>
              </v-flex>



   
  </div>


  <v-flex d-flex md4 xs10 mt-5  >

     <v-btn  class="btn-float btn-float-left-2 btn-float-l-2" color="blue" @click="crear()">Crear Preferencial</v-btn>
  </v-flex>

<div id="profile" class="box-style box-style-l ">

<div id="count" class="">
  <i class="fas fa-address-card icon-inf"></i>
  <h3 class="text text-sub--itemsub blue--text label-item">LISTADO CLIENTES PREFERENCIALES</h3>
</div>
<v-data-table
      :headers="headers"
      :items="preferencial"
    
      class="elevation-1 table-activate"
    >
      <template v-slot:items="props">
        <td class="text text-sub--itemsub">{{ props.item.placa }}</td>
        <td class=" text text-sub--itemsub">{{ props.item.valorp}}</td>
        <td class=" text text-sub--itemsub">{{ props.item.concepto}}</td>
        <td class=" text text-sub--itemsub">{{ props.item.descripcion }}</td>
        <td class="text text-sub--itemsub"><v-btn @click="eliminar_preferencial(props.item.placa,props.index)" class="btn-act orange">Eliminar</v-btn></td>
        


      </template>
    </v-data-table>
</div>
  <v-snackbar
         id="modalig"
         class="mss-modal  v-snack--active2"
         v-model='show'
         top

       >
       <v-flex  md12 d-flex>
       <!--<v-img class="slide-right mr-2" v-bind:src="require('../scss/resources/SVG/'+pathIcon)" width="80"></v-img>-->
       <span class="text text-sub--item  text-md-center text-xs-center">{{texto}}</span>
       </v-flex>
       
     
    </v-snackbar>


</div>




  </v-layout>
</template>

<script>

export default {

  data(){
    return{
    placa:"",
    valor:"",
    concepto: "",
    tari_seleccionada : "",
    placa_delete : "",
    posi : -1,

headers: [
          { text: 'Placa', value: 'placa' },
          { text: 'Valor', value: 'valor' },
          { text: 'Tarifa', value: 'tarifa' },
          { text: 'Concepto', value: 'concepto' },
           { text: 'Operación', value: 'operación' },
          
        ],
    preferencial : [],
    texto : null,
    show : false,
    pathIcon:'active.svg',
    
    tarifas : [
      {
       concepto : 'Vehiculo Particular Especial',
       id     : 3
      },
      {
       concepto : 'Moto Particular Especial',
       id     : 4
      },
      {
       concepto : 'Vehiculo Transporte Público Especial',
       id     : 5
      },
      {
       concepto : 'Carro mensual',
       id     : 9
      },
      {
       concepto : 'Moto mensual',
       id     : 10
      },
    ]
    }
  },
   
mounted() {
  this.carga();  

},
methods: {

 async carga()
  {
   var vm = this;
   await this.axios({
     method: 'get',
     url: 'http://107.21.217.207:8000/admin/all_preferencial',
     })
     .then(function (response) {
       vm.preferencial = response.data.data;
       console.log(response.data.data);
     });
  },

   async eliminar_preferencial(placa,posi)
   {
               this.placa_delete = placa;
               this.posi = posi;
               var vm = this;
               await this.axios({
               method: 'post',
               url: 'http://107.21.217.207:8000/admin/eliminar_preferencial',
               data: {
                 placa :  vm.placa_delete,
                }
               })
                 .then(function (response) {
                  console.log(response.data);
                  vm.preferencial.splice(posi,1);
                  vm.texto = response.data.info;
                  vm.show = true;
                 })
               
   },

   async crear()
    {
    if ((this.placa !== "") && (this.valor !== "") && (this.concepto !== "") && (this.tari_seleccionada !== ""))
      {
       this.placa = this.placa.trim();
       this.valor = this.valor.trim();
       this.placa = this.placa.toUpperCase();
               var vm = this;
               await this.axios({
               method: 'post',
               url: 'http://107.21.217.207:8000/admin/crear_preferencial',
               data: {
                 placa :  vm.placa,
                 valorp : vm.valor,
                 id_tarifa : vm.tari_seleccionada,
                 descripcion : vm.concepto,
                }
               })
               .then(function (response) {
                  console.log(response.data);
                  vm.texto = response.data.info;
                  vm.show = true;
                  vm.placa = "";
                  vm.valor = "";
                  vm.concepto = "";
                  vm.tari_seleccionada = "";
                  vm.carga();
               });
            
            }
     else
       {
          this.texto = "Hay campos vacios ..."
          this.show = true;
        }
    }
    
    }
}

</script>
