<template>

   <v-layout row wrap>

    
  <div id="informe" class="box-style">
  
  <v-card class="white card-act pb-4">
        

        <v-card-text class="text-md-center text-sm-center text-xs-center header_act">

            <v-layout d-block>
            
            
             <v-form ref="form">
              <v-flex xs12 md12 sm12 class="text-md-center mt-4">
       
       <v-flex md12 black--text text-md-center text-xs-center text-sm-center>
      <h1 class="text text-sub--itemsub weitng mb-2"></h1>
      <v-text-field append-icon="mic" id="search" v-model="placa" class="mx-1 input_seach mb-2" flat solo-inverted hide-details label="Buscar placa" prepend-inner-icon="search"></v-text-field>
    </v-flex>

              <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="date"
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="fecha_inicial"
            label="Fecha Inicial"
            prepend-icon="event"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker v-model="fecha_inicial" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu=false">Cancel</v-btn>
          <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
        </v-date-picker>
      </v-menu>
               
              </v-flex>
              <v-flex xs12 md12 sm12 class="text-md-center mt-4">
              <v-menu
        ref="menu2"
        v-model="menu2"
        :close-on-content-click="false"
        :return-value.sync="date"
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="fecha_final"
            label="Fecha Final"
            prepend-icon="event"
            readonly
            v-on="on"
          ></v-text-field>
          
        </template>
        <v-date-picker v-model="fecha_final" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="menu=false">Cancel</v-btn>
          <v-btn text color="primary" @click="$refs.menu2.save(date)">OK</v-btn>
        </v-date-picker>
      </v-menu>



             
              </v-flex>
           </v-form>
         </v-layout>
      </v-card-text>
     <v-divider></v-divider>
     <v-card-actions>
     <v-spacer></v-spacer>
     <v-flex d-flex text-md-right>
         <v-btn  color="blue darken-1"  @click="validador()">Buscar</v-btn>
     </v-flex>
     </v-card-actions>
     </v-card> 
  </div>
   <div id="informe" class="box-style">
     <h3 class="text text-sub--itemsub  blue--text  label-item ">RESULTADO</h3>
   <v-data-table
      :headers="headers"
      :items="registros"
    
      class="elevation-1 table-activate"
    >
      <template v-slot:items="props">
        <td class="text text-sub--itemsub">{{ props.item.placa }}</td>
        <td class=" text text-sub--itemsub">{{ props.item.fecha_entrada}}</td>
        <td class=" text text-sub--itemsub">{{ props.item.fecha_salida}}</td>
        
        


      </template>
    </v-data-table>
   
    </div>

  
<!--FIN SALIDA VEHICULO -->
<pre>
   {{$data}}
   </pre>

<v-snackbar
         id="modalig"
         class="mss-modal  v-snack--active2"
         v-model='show'
         top

       >
       <v-flex  md12 d-flex>
           <span class="text text-sub--item  text-md-center text-xs-center">{{texto}}</span>
       </v-flex>
       
     
    </v-snackbar>

  
</v-layout>


</template>
<script>
 
  export default {
    
    data () {
      return {
        headers: [
          { text: 'Placa', value: 'placa' },
          { text: 'Hora Entrada', value: 'valor' },
          { text: 'Hora Salida', value: 'tarifa' },
           ],
       

       placa: "",
       fecha_final : "",
       fecha_inicial :"",

       registros : [],
       
       timeout:4000,
       show:false,
      
       
      }
		},
    
		methods:{

    validador()
    {
      if ((this.fecha_final !== "") && (this.fecha_inicial !== "") && (this.placa !== ""))
        {
         if (this.fecha_inicial > this.fecha_final)
          {
            this.texto = "La fecha inicial no puede ser mayor a la fecha final";
            this.show = true;
            this.fecha_inicial = "";
          }
         else
         {
           this.busqueda();
         }
        
        }
      else
       {
         this.texto = "No puede exister campos vacios";
         this.show = true;
       }
       
    },

     busqueda()
    {

          var vm = this;
          this.placa = this.placa.toUpperCase();
          this.axios({
           method: 'post',
           url: 'http://107.21.217.207:8000/admin/informe_placa',
            data: {
              fecha_inicial : vm.fecha_inicial,
              fecha_final :  vm.fecha_final,
              placa : vm.placa,
            }
          }).then(function (response) {
              vm.registros = response.data.info;
              console.log("consulta: " + vm.registros);
          })
     
      }
  }
  }
</script>
<style >
	#comprar{
		border-radius: 20px;
	}
</style>
